  padding: 7px 10px 7px;
  will-change: transform, opacity;
  transition: opacity 0.35s ease, transform 0.35s ease;
  font-size: 12px;
  font-weight: 600;
  transform: translate3d(0, -6px, 0);
  background: #333;
  word-wrap: break-word;
  max-width: 250px;
  text-align: center;
}

.tooltip-container.error-tooltip {
